<script>
	import * as THREE from '../../lib/three.weapp.js'
	import {
		OrbitControls
	} from '../../lib/OrbitControls.js'
	import GLTF from '../../lib/GLTFLoader.js'
	import { getHealthReport, getHealthAdvice } from '@/api/user';

	export default {
		data() {
			return {
				mSceneWidth: 0,
				showPopup: false,
				mSceneHeight: 0,
				modelHeight: 280, // 3DÊ®°ÂûãÂå∫ÂüüÈ´òÂ∫¶
				worldFocus: null,
				mCanvasId: null,
				mScene: null,
				mCamera: null,
				renderer: null,
				renderAnimFrameId: null,
				closePopupTimer: null,
				safeAreaInsets: uni.getSystemInfoSync().safeAreaInsets || {
					top: 0
				}
			};
		},
		// È°µÈù¢Âä†ËΩΩÊó∂
		onLoad(option) {
			this.getAdvice()
			// Ëé∑ÂèñÊâãÊú∫Â±èÂπïÂÆΩÈ´ò
			this.mSceneWidth = uni.getWindowInfo().windowWidth;
			this.mSceneHeight = this.modelHeight; // ËÆæÁΩÆ3DÊ®°ÂûãÂå∫ÂüüÈ´òÂ∫¶
			// ËÆæÁΩÆ‰∏ñÁïå‰∏≠ÂøÉ
			this.worldFocus = new THREE.Vector3(0, 0, 0);
		},
		// È°µÈù¢Âä†ËΩΩÂÆåÊØïÂêé
		onReady() {
			uni.createSelectorQuery().select('#scene').node().exec((res) => {
				if (!res[0] || !res[0].node) {
					console.error("Ëé∑Âèñ canvas Â§±Ë¥•");
					return;
				}
				this.mCanvasId = res[0].node._canvasId;
				console.log("mCanvasId:", this.mCanvasId);
				const mCanvas = THREE.global.registerCanvas(this.mCanvasId, res[0].node);
				this.init(mCanvas);
			});
		},
		// È°µÈù¢Âç∏ËΩΩÊó∂
		onUnload() {
			console.log("Unload", this.mCanvasId);

			if (this.renderAnimFrameId && THREE.global.canvas) {
				THREE.global.canvas.cancelAnimationFrame(this.renderAnimFrameId);
			}

			if (this.mCanvasId) {
				THREE.global.unregisterCanvas(this.mCanvasId);
			} else {
				console.warn("mCanvasId ‰∏∫Á©∫ÔºåË∑≥Ëøá unregisterCanvas");
			}
		},
		methods: {
			async getAdvice(){
				const {data} = getHealthAdvice()
				console.log(data)
				console.log(data)
				console.log(data)
				console.log(data)
			},
			// ÂàùÂßãÂåñ
			init(canvas) {
				// ÂàõÂª∫Âú∫ÊôØ
				this.mScene = new THREE.Scene();
				this.mScene.background = new THREE.Color("#e6e6e6"); // Âú∫ÊôØËÉåÊôØÈ¢úËâ≤

				// ÂàõÂª∫ÊëÑÂÉèÊú∫
				this.mCamera = new THREE.PerspectiveCamera(75, this.mSceneWidth / this.mSceneHeight, 0.1, 100);
				this.mCamera.position.set(0, 0, 20);
				this.mCamera.lookAt(this.worldFocus);

				// ÂàõÂª∫ÂÖâÁ∫ø
				const light1 = new THREE.HemisphereLight(0xffffff, 0x444444); // ÂçäÁêÉÂÖâÔºàÂ§©Á©∫È¢úËâ≤ÔºåÂú∞Èù¢È¢úËâ≤ÔºåÂÖâÁÖßÂº∫Â∫¶Ôºâ
				light1.position.set(0, 20, 0);
				this.mScene.add(light1);
				const light2 = new THREE.DirectionalLight(0xffffff); // Âπ≥Ë°åÂÖâÔºàÈ¢úËâ≤Ôºå ÂÖâÁÖßÂº∫Â∫¶Ôºâ
				light2.position.set(0, 0, 20);
				this.mScene.add(light2);
				this.mScene.background = null;


				// ÂàõÂª∫Ê∏≤ÊüìÂô®
				const renderer = new THREE.WebGLRenderer({
					antialias: true,
					alpha: true,

				});
				renderer.setSize(this.mSceneWidth, this.mSceneHeight);

				// ÂàõÂª∫ÊéßÂà∂Âô®
				const controls = new OrbitControls(this.mCamera, renderer.domElement);
				controls.target.set(this.worldFocus.x, this.worldFocus.y, this.worldFocus.z); // ÊëÑÂÉèÊú∫Âõ¥ÁªïÊóãËΩ¨ÁöÑ‰∏≠ÂøÉ
				controls.enablePan = false; // Á¶ÅÊ≠¢ÊëÑÂÉèÊú∫Âπ≥Áßª
				controls.enableDamping = true; // ËÆæÁΩÆÈòªÂ∞ºÔºåÈúÄË¶ÅÂú® update Ë∞ÉÁî®
				controls.dampingFactor = 0.09;

				// ÂàõÂª∫ glb Âä†ËΩΩÂô®
				let GLTFloader = GLTF(THREE)
				const loader = new GLTFloader();
				// ÂºÇÊ≠•Âä†ËΩΩÊ®°Âûã
				// ÂæÆ‰ø°Â∞èÁ®ãÂ∫è‰∏çÂÖÅËÆ∏Âä†ËΩΩÊú¨Âú∞Ê®°ÂûãÔºåÂøÖÈ°ªÈÄöËøá https Ëé∑Âèñhttps://threejs.org/examples/models/gltf/Stork.glb
				loader.load("https://soevereign.oss-cn-shenzhen.aliyuncs.com/man.gltf", (gltf) => {
					const model = gltf.scene;
					model.position.set(0, 5, 0); // ËÆæÁΩÆÊ®°Âûã‰ΩçÁΩÆ
					model.scale.set(25, 25, 25); // ËÆæÁΩÆÊ®°ÂûãÂ§ßÂ∞è
					model.userData.info = "ËøôÊòØ‰∏Ä‰ΩçËôöÊãü‰∫∫Áâ©ÔºåÊù•Ëá™Êú™Êù•‰∏ñÁïå„ÄÇ";
					this.mScene.add(model);
					// Ê®°ÂûãÂä†ËΩΩÂà∞Âú∫ÊôØÂêéÔºåÂºÄÂêØÊ∏≤Êüì
					render();
				});

				// Ê∏≤ÊüìÔºàÈó≠ÂåÖÔºâ
				var render = () => {
					// Â∏ßÂä®Áîª
					this.renderAnimFrameId = canvas.requestAnimationFrame(render);

					//ÂÖâÊ∫êË∑üÈöèÁõ∏Êú∫
					var vector = this.mCamera.position.clone();
					light2.position.set(vector.x, vector.y, vector.z);

					// ÊéßÂà∂Âô®
					if (controls.enabled) controls.update();
					// Ê∏≤ÊüìÂú∫ÊôØ
					renderer.render(this.mScene, this.mCamera);
				};
			},
			// Ëß¶Êë∏ÂºÄÂßã
			touchStart(e) {
				const touch = e.touches[0];
				const raycaster = new THREE.Raycaster();
				const mouse = new THREE.Vector2();

				// ËÆ°ÁÆóÁÇπÂáª‰ΩçÁΩÆ
				mouse.x = (touch.clientX / this.mSceneWidth) * 2 - 1;
				mouse.y = -(touch.clientY / this.mSceneHeight) * 2 + 1;

				// ËÆ°ÁÆóÂ∞ÑÁ∫ø
				raycaster.setFromCamera(mouse, this.mCamera);

				// Ê£ÄÊµã‰∫§ÂèâÁÇπ
				const intersects = raycaster.intersectObjects(this.mScene.children, true);
				if (intersects.length > 0) {
					const clickedObject = intersects[0].object;
					console.log("ÁÇπÂáªÁöÑÂØπË±°:", clickedObject);
					console.log("ÁÇπÂáªÂØπË±°ÁöÑ userData:", clickedObject.userData);
				} else {
					console.log("Êú™ÁÇπÂáªÂà∞Ê®°Âûã");
				}

				THREE.global.touchEventHandlerFactory('canvas', 'touchstart')(e);
			},
			// Ëß¶Êë∏ÁßªÂä®‰∏≠
			touchMove(e) {
				THREE.global.touchEventHandlerFactory('canvas', 'touchmove')(e)
			},
			// Ëß¶Êë∏ÁªìÊùü
			touchEnd(e) {
				// Â¶ÇÊûú‰πãÂâçÊúâÂÆöÊó∂Âô®ÔºåÊ∏ÖÈô§ÂÆÉ
				if (this.closePopupTimer) {
					clearTimeout(this.closePopupTimer);
				}

				// ËÆæÁΩÆÊñ∞ÁöÑÂÆöÊó∂Âô®ÔºåÂª∂ËøüÂÖ≥Èó≠ÂºπÊ°Ü
				this.closePopupTimer = setTimeout(() => {
					this.showPopup = false; // ÂÖ≥Èó≠ÂºπÊ°Ü
				}, 1500); // 1.5ÁßíÂêéÂÖ≥Èó≠ÂºπÊ°Ü
			}
		}
	};
</script>

